
.header {
  color: #fff;
  background: #ccc;
}
.header .content-wrapper {
    position: relative;
    padding: 24px 12px 18px 24px;
    font-size: 0;
}
.header .content-wrapper .avatar {
      display: inline-block;
      vertical-align: top;
}
.header .content-wrapper .avatar img {
        border-radius: 2px;
}
.header .content-wrapper .content {
      display: inline-block;
      margin-left: 16px;
}
.header .content-wrapper .content .title {
        margin: 2px 0 8px 0;
}
.header .content-wrapper .content .title .brand {
          display: inline-block;
          width: 32px;
          height: 18px;
          vertical-align: top;
          background-image: url("brand@2x.png");
          background-repeat: no-repeat;
          background-size: 32px 18px;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .title .brand {
              background-image: url("brand@3x.png");
}
}
.header .content-wrapper .content .title .name {
          margin-left: 6px;
          font-size: 16px;
          font-weight: bold;
          line-height: 18px;
}
.header .content-wrapper .content .description {
        margin-bottom: 10px;
        font-size: 12px;
        line-height: 12px;
}
.header .content-wrapper .content .supports .icon {
        display: inline-block;
        margin-right: 4px;
        width: 12px;
        height: 12px;
        background-repeat: no-repeat;
        background-size: 12px 12px;
}
.header .content-wrapper .content .supports .icon.decrease {
          background-image: url("decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .supports .icon.decrease {
              background-image: url("decrease_1@3x.png");
}
}
.header .content-wrapper .content .supports .icon .discount {
          background-image: url("discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .supports .icon .discount {
              background-image: url("discount_1@3x.png");
}
}
.header .content-wrapper .content .supports .icon .guarantee {
          background-image: url("guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .supports .icon .guarantee {
              background-image: url("guarantee_1@3x.png");
}
}
.header .content-wrapper .content .supports .icon .invoice {
          background-image: url("invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .supports .icon .invoice {
              background-image: url("invoice_1@3x.png");
}
}
.header .content-wrapper .content .supports .icon .special {
          background-image: url("special_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .supports .icon .special {
              background-image: url("special_1@3x.png");
}
}
.header .content-wrapper .content .supports .text {
        vertical-align: top;
        font-size: 10px;
        line-height: 12px;
}
.header .content-wrapper .support-count {
      position: absolute;
      right: 12px;
      bottom: 18px;
      padding: 0 8px;
      border-radius: 12px;
      height: 24px;
      line-height: 24px;
      background: rgba(0, 0, 0, 0.2);
      text-align: center;
}
.header .content-wrapper .support-count .count {
        font-size: 10px;
}
.header .content-wrapper .support-count .icon-arrow_lift {
        font-size: 10px;
        margin-left: 2px;
        vertical-align: top;
}



